.formulario{
    display: flex;
    justify-content: center;
    margin:  80px 0;
}

.formulario form {
    max-width: 80%;
    background-color: #F7F7F7;
    border-radius: 15px;
    box-shadow: 7px 7px 15px 0px rgba(0, 0, 0, 25%);
    padding: 8px 100px;
}

.formulario h2 {
    font-size: 32px;
    font-weight: 400;
    color: #212121;
}